import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";
import moment from "moment";
import NumberFormat from "../../../App/NumberFormat";

import notification from "../../../../components/notification";
import {
  Empty,
  Collapse,
  Select,
  DatePicker,
  Row,
  Col,
  Divider,
  Input
} from "antd";

// import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/report/issueReceiptReport/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, {
  Box,
  StatusTag,
  StatusBadge,
  TransNo,
  customCollapseStyle
} from "../../../App/app.style";
import PrintWrapper from "../../../App/PrintWrapper";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import ExcelWrapper from "../../../App/ExcelWrapper";
import PDFWrapper from "../../../App/PDFWrapper";
import getLanguage from "../../../App/MyLanguange";
import { getFileName } from "../../../../helpers/helper";
import { siteConfig } from "../../../../settings";

const Panel = Collapse.Panel;
const Option = Select.Option;
const Search = Input.Search;
const { RangePicker } = DatePicker;

const redirectTo = "/dashboard/report-menus";
const redirectIR = "/dashboard/inventories/issue-receipts";

class IssueReceiptReport extends Component {
  state = {
    filter: {
      id_location: null,
      status: null,
      id_adjustment_type: null,
      start_date: moment()
        .startOf("month")
        .format("YYYY-MM-DD HH:mm:ss"),
      end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
      sort_by: "transaction_date",
      sort_type: "asc"
      // pagination: null
    },
    search: null
  };

  // Get All data diawal
  componentDidMount() {
    const { getIssueReceiptReports } = this.props;
    getIssueReceiptReports();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.issueReceiptReport.error) {
      notification("error", this.props.issueReceiptReport.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.issueReceiptReport.success) {
      notification("success", this.props.issueReceiptReport.success);
      this.props.clearSuccess();
    }
  }

  // Handle search
  handleSearch = value => {
    const { getIssueReceiptReports } = this.props;
    let filter = this.filterValue();

    getIssueReceiptReports(1, value, filter, this.state.filter);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getIssueReceiptReports } = this.props;
    this.setState({
      filter: {
        id_location: null,
        status: null,
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        sort_by: "transaction_date",
        sort_type: "asc"
        // pagination: null
      },
      search: null
    });
    getIssueReceiptReports();
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  filterValue = () => {
    const { filter } = this.state;
    let string = "";

    if (filter.status) {
      string += "status=" + filter.status;
    }
    if (filter.id_adjustment_type) {
      string
        ? (string += "&id_adjustment_type=" + filter.id_adjustment_type)
        : (string += "id_adjustment_type=" + filter.id_adjustment_type);
    }
    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
            "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
            "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    }
    if (filter.id_location) {
      string
        ? (string += "&id_location=" + filter.id_location)
        : (string += "id_location=" + filter.id_location);
    }
    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }

    string
      ? (string +=
          "&sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type)
      : (string +=
          "sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type);

    return string;
  };

  // Handle filter
  handleFilter = () => {
    const { getIssueReceiptReports } = this.props;
    //const { current_page } = this.props.issueReceiptReport;
    let filter = this.filterValue();

    this.setState({ search: null });
    getIssueReceiptReports(1, null, filter, this.state.filter);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getIssueReceiptReports } = this.props;
    this.setState({
      filter: {
        id_location: null,
        status: null,
        id_adjustment_type: null,
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        sort_by: "transaction_date",
        sort_type: "asc"
        // pagination: null
      }
    });
    getIssueReceiptReports();
  };

  render() {
    // Definisi kolom2 serta field2 API yang dipakai tabel
    let columns = [
      // {
      //   title: "No.",
      //   dataIndex: "key",
      //   rowKey: "key",
      //   width: "5%",
      //   align: "center",
      //   render: (text, row) => {
      //     if (row.issue_receipt_no === "Total") {
      //       return {
      //         children: <strong>{text}</strong>,
      //         props: {
      //           colSpan: 6
      //         }
      //       };
      //     }

      //     return text;
      //   }
      // },
      {
        title: getLanguage({ id: "issueReceipts.issueReceiptNo" }),
        dataIndex: "issue_receipt_no",
        rowKey: "issue_receipt_no",
        //width: "20%",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 6,
        render: (text, row) => {
          if (text === "Total") {
            return {
              children: (
                <span style={{ marginLeft: "50%" }}>
                  <strong>{row.issue_receipt_no}</strong>
                </span>
              ),
              props: {
                colSpan: 6
              }
            };
          } else {
            let className;
            switch (row.status) {
              case 1:
                className = "pending";
                break;
              case 2:
                className = "confirmed";
                break;
              case 3:
                className = "cancelled";
                break;
              default:
                className = "new";
                break;
            }

            return (
              <div style={{ display: "flex", height: "20px" }}>
                <StatusBadge className={className} />
                <TransNo>
                  <Link to={`${redirectIR}/update/${row.id_issue_receipt}`}>
                    {text}
                  </Link>
                </TransNo>
              </div>
            );
            // return <StatusTag className={className}>{display}</StatusTag>;
          }
        }
      },
      {
        title: getLanguage({ id: "transactionDate" }),
        date: true,
        dataIndex: "transaction_date",
        rowKey: "transaction_date",
        //width: "15%",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 0,
        render: (text, row) => {
          if (row.issue_receipt_no === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>;
        }
      },
      {
        title: getLanguage({ id: "sidebar.adjustmentType" }),
        dataIndex: "adjustment_type_name",
        rowKey: "adjustment_type_name",
        //width: "15%",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 0,
        render: (text, row) => {
          if (row.issue_receipt_no === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return text;
        }
      },
      {
        title: getLanguage({ id: "sidebar.location" }),
        dataIndex: "location_name",
        rowKey: "location_name",
        //width: "15%",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 0,
        render: (text, row) => {
          if (row.issue_receipt_no === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return text;
        }
      },
      {
        title: getLanguage({ id: "confirmedBy" }),
        dataIndex: "confirmed_by",
        rowKey: "confirmed_by",
        width: "10%",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 0,
        render: (text, row) => {
          if (row.issue_receipt_no === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          return text;
        }
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "status_string",
        rowKey: "status_string",
        width: "10%",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 0,
        align: "center",
        render: (text, row) => {
          if (row.issue_receipt_no === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          }

          let className;
          let display;
          switch (row.status) {
            case 1:
              className = "pending";
              display = getLanguage({ id: "pending" });
              break;
            case 2:
              className = "confirmed";
              display = getLanguage({ id: "confirmed" });
              break;
            case 3:
              className = "cancelled";
              display = getLanguage({ id: "cancelled" });
              break;
            default:
              className = "pending";
              display = getLanguage({ id: "pending" });
              break;
          }

          return <StatusTag className={className}>{display}</StatusTag>;
        }
      },
      {
        title: getLanguage({ id: "total" }),
        dataIndex: "total_amount",
        rowKey: "total_amount",
        align: "right",
        //width: "15%",
        parse: true,
        render: (text, row) => {
          if (row.issue_receipt_no === "Total") {
            return (
              <strong>
                <NumberFormat value={parseFloat(text)} />
              </strong>
            );
          }

          return <NumberFormat value={parseFloat(text)} />;
        }
      }
    ];

    // Data setup
    const {
      issue_receipt_reports,
      locations,
      adjustment_types,
      filter,
      filter_select
    } = this.props.issueReceiptReport;
    //console.log(this.props.issueReceiptReport.base_currency); //console.log(this.state);
    const dataSource = [];

    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    let total = 0;
    issue_receipt_reports.map((data, key) => {
      let status_string;
      if (data.status === 2) {
        status_string = getLanguage({ id: "confirmed" });
      } else if (data.status === 3) {
        status_string = getLanguage({ id: "cancelled" });
      } else {
        status_string = getLanguage({ id: "pending" });
      }

      total += parseFloat(data.total_amount);
      dataSource.push({
        ...data,
        status_string,
        key: key + 1
      });
    });

    dataSource.push({
      key: -11,
      issue_receipt_no: "Total",
      total_amount: total
    });
    // console.log(issue_receipt_reports, dataSource);
    const { loading, loadingForeign } = this.props.issueReceiptReport;

    const fileName = getLanguage({ id: "issueReceiptReports.pageHeader" })
    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="issueReceiptReports.pageHeader" />}
            marginBottom={true}
          >
            <PrintWrapper
              componentRef={ref => (this.componentRef = ref)}
              title={<IntlMessages id="sidebar.issueReceipt" />}
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
            />

            <ReactToPrint
              trigger={() => <ButtonPrint loading={loading} />}
              content={() => this.componentRef}
              onBeforePrint={() => (document.title = getFileName(fileName, null, false))}
              onAfterPrint={() => document.title = siteConfig.documentTitle}
              pageStyle={
                "@page { size: landscape; }" +
                "body {" +
                "-webkit-print-color-adjust: exact;" +
                "} "
              }
            />

            <PDFWrapper
              columns={columns}
              data={dataSource}
              fileName={fileName}
              filter={filter_select}
              landscape={true}
              loading={loading}
              locations={locations}
              scale={0.58}
              title="sidebar.issueReceipt"
            />

            <ExcelWrapper
              title="sidebar.issueReceipt"
              fileName={fileName}
              loading={loading}
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
            />

            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <div className="isoInvoiceTableBtn">
            <Search
              placeholder={
                getLanguage({ id: "issueReceipts.issueReceiptNo" }) +
                ", " +
                getLanguage({ id: "confirmedBy" })
              }
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.searchFilter" />
              </Button>
            </div>

            <div
              style={{
                width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle} defaultActiveKey={["1"]}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="locations.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_location"
                      )}
                      value={
                        this.state.filter.id_location
                          ? this.state.filter.id_location
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={<IntlMessages id="select.location" />}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {`${data.location_code} (${data.location_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>

                  <Col span={12}>
                    <label>
                      <IntlMessages id="sidebar.adjustmentType" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_adjustment_type"
                      )}
                      value={
                        this.state.filter.id_adjustment_type
                          ? this.state.filter.id_adjustment_type
                          : []
                      }
                      style={{ width: "100%" }}
                      placeholder={<IntlMessages id="sidebar.adjustmentType" />}
                      optionFilterProp="children"
                      filterOption={true}
                      // loading={this.props.to_location.loadingFilterSites}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {adjustment_types.map((data, key) => (
                        <Option key={key} value={data.id_adjustment_type}>
                          {`${data.adjustment_type_code} (${data.description})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="salesOrders.filter.date" />
                    </label>
                    <br />
                    <RangePicker
                      format="DD MMMM YYYY HH:mm:ss"
                      showTime
                      allowClear={false}
                      style={{ width: "95%" }}
                      value={
                        this.state.filter.start_date &&
                        this.state.filter.end_date
                          ? [
                              moment(this.state.filter.start_date),
                              moment(this.state.filter.end_date)
                            ]
                          : []
                      }
                      onChange={this.handleFilterChange.bind(this, "date")}
                    />
                  </Col>
                  <Col span={12}>
                    <Row justify="start" gutter={1} type="flex">
                      <Col span={12}>
                        <label>
                          <IntlMessages id="stockValuationReports.sortBy" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "sort_by"
                          )}
                          value={
                            this.state.filter.sort_by
                              ? this.state.filter.sort_by
                              : "transaction_date"
                          }
                          style={{ width: "95%" }}
                          // placeholder={
                          //   <IntlMessages id="stockValuationReports.sortBy.select" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option value="transaction_date">
                            <IntlMessages id="issueReceiptReports.filter.date" />
                          </Option>
                          <Option value="issue_receipt_no">
                            <IntlMessages id="issueReceipts.issueReceiptNo" />
                          </Option>
                          <Option value="adjustment_type_name">
                            <IntlMessages id="sidebar.adjustmentType" />
                          </Option>
                          <Option value="total_amount">
                            <IntlMessages id="totalAmount" />
                          </Option>
                        </Select>
                      </Col>
                      <Col span={12}>
                        <label>
                          <IntlMessages id="sortType" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "sort_type"
                          )}
                          value={
                            this.state.filter.sort_type
                              ? this.state.filter.sort_type
                              : "asc"
                          }
                          style={{ width: "100%" }}
                          // placeholder={
                          //   <IntlMessages id="stockValuationReports.sortBy.select" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option key={1} value="asc">
                            <IntlMessages id="ascending" />
                          </Option>
                          <Option key={2} value="desc">
                            <IntlMessages id="descending" />
                          </Option>
                        </Select>
                      </Col>
                    </Row>
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "24px" }}
                >
                  <Col span={12}>
                    <Row justify="start" gutter={1} type="flex">
                      <Col span={8}>
                        <label>Status</label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "status"
                          )}
                          value={
                            this.state.filter.status
                              ? this.state.filter.status
                              : undefined
                          }
                          style={{ width: "95%" }}
                          placeholder="Status"
                          optionFilterProp="children"
                          filterOption={true}
                          allowClear={true}
                          //showArrow={false}
                        >
                          <Option value={1}>
                            {getLanguage({ id: "pending" })}
                          </Option>
                          <Option value={2}>
                            {getLanguage({ id: "confirmed" })}
                          </Option>
                          <Option value={3}>
                            {getLanguage({ id: "cancelled" })}
                          </Option>
                        </Select>
                      </Col>

                      <Col
                        span={8}
                        style={{
                          marginTop: "auto"
                        }}
                      >
                        <Button
                          size="small"
                          type="primary"
                          style={{ marginRight: "10px" }}
                          onClick={this.handleFilter}
                        >
                          <IntlMessages id="filter.only" />
                        </Button>
                        <Button onClick={this.handleResetFilter} size="small">
                          <IntlMessages id="filter.reset" />
                        </Button>
                      </Col>
                    </Row>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {!filter ? (
              <Empty description={<IntlMessages id="clickFilter" />} />
            ) : issue_receipt_reports.length === 0 &&
              !this.props.issueReceiptReport.loading ? (
              <Empty
                description={
                  <IntlMessages id="issueReceiptReports.table.noData" />
                }
              />
            ) : (
              <div className="isoInvoiceTable">
                <Divider orientation="left" style={{ fontSize: 14 }}>
                  <IntlMessages id="details" />
                </Divider>
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={dataSource}
                    columns={columns}
                    loading={loading}
                    className="invoiceListTable forceHeight"
                    bordered={true}
                    // Set pagination, jika ada page_size mengunakan pagination API
                    // jika tidak ada page_size menggunakan pagination bawaan
                    pagination={false}
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    issueReceiptReport: state.issueReceiptReport,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(IssueReceiptReport);
